import {InputFragment} from "../../abstract/InputFragment"

export abstract class Field extends InputFragment<string|number|boolean|(string|number|boolean)[]|null>{

    protected constructor(location: FragmentLocation,
                          InputClass: new<T>(location: FragmentLocation, config: InputConfig) => InputFragment<T>,
                          inputConfig: InputConfig
    ) {
        super(location)
        new InputClass({target: this.core, position: "afterbegin"}, inputConfig)
    }
}